<template>
	<view class="container">
		<view class="progress-bar">
			<text class="progress-text">4/6</text>
		</view>

		<view class="question-container">
			<view class="emoji">üòÑ</view>
			<text class="question">ÊÇ®ÊÉ≥Êä•ËÄÉÁöÑÁ†îÁ©∂Áîü‰∏ì‰∏öÊòØÔºü</text>
			
			<view class="search-box">
				<input class="search-input" 
					v-model="searchText" 
					placeholder="ËØ∑ÈÄâÊã©‰∏ì‰∏ö" 
					@focus="showMajorList = true"
				/>
			</view>
			
			<!-- ‰∏ì‰∏öÂàóË°® -->
			<scroll-view 
				v-if="showMajorList" 
				class="major-list"
				scroll-y="true"
			>
				<view 
					v-for="(major, index) in filteredMajors" 
					:key="index"
					class="major-item"
					@click="selectMajor(major)"
				>
					{{major}}
				</view>
			</scroll-view>
			
			<text class="question second-question">Â∏åÊúõÂ∞±ËØªÁöÑÂ≠¶‰π†ÊñπÂºèÊòØÔºü</text>
			<view class="options-container">
				<view 
					v-for="(option, index) in studyModes" 
					:key="index"
					:class="['option-item', selectedMode === option ? 'active' : '']"
					@click="selectMode(option)"
				>
					{{option}}
				</view>
			</view>
		</view>

		<view class="button-group">
			<button class="prev-btn" @click="prevPage">‰∏ä‰∏ÄÊ≠•</button>
			<button class="next-btn" @click="nextPage">‰∏ã‰∏ÄÊ≠•</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				searchText: '',
				showMajorList: false,
				majors: [],
				studyModes: ['ÂÖ®Êó•Âà∂', 'ÈùûÂÖ®Êó•Âà∂'],
				selectedMajor: '',
				selectedMode: '',
				loading: false
			}
		},
		computed: {
			filteredMajors() {
				return this.majors.filter(major => 
					major.toLowerCase().includes(this.searchText.toLowerCase())
				);
			}
		},
		onLoad() {
			// Âä†ËΩΩ‰∏ì‰∏öÊï∞ÊçÆ
			this.getMajorData();
		},
		methods: {
			// Ëé∑Âèñ‰∏ì‰∏öÊï∞ÊçÆ
			async getMajorData() {
				this.loading = true;
				try {
					const result = await uniCloud.callFunction({
						name: 'getMajors',
						data: {}
					});
					if (result.result.code === 0) {
						this.majors = result.result.data;
					}
				} catch (e) {
					console.error('Ëé∑Âèñ‰∏ì‰∏öÊï∞ÊçÆÂ§±Ë¥•:', e);
					uni.showToast({
						title: 'Ëé∑Âèñ‰∏ì‰∏öÊï∞ÊçÆÂ§±Ë¥•',
						icon: 'none'
					});
				} finally {
					this.loading = false;
				}
			},
			
			// Ê†πÊçÆÂÖ≥ÈîÆËØçÊêúÁ¥¢‰∏ì‰∏ö
			async searchMajors(keyword) {
				if (!keyword) return;
				this.loading = true;
				try {
					const result = await uniCloud.callFunction({
						name: 'getMajors',
						data: { keyword }
					});
					if (result.result.code === 0) {
						// Âè™Âú®ÊúâÁªìÊûúÊó∂Êõ¥Êñ∞ÂàóË°®ÔºåÈÅøÂÖçÊ∏ÖÁ©∫
						if (result.result.data.length > 0) {
							this.majors = result.result.data;
						}
					}
				} catch (e) {
					console.error('ÊêúÁ¥¢‰∏ì‰∏öÂ§±Ë¥•:', e);
				} finally {
					this.loading = false;
				}
			},
			
			// ÊêúÁ¥¢Ê°ÜËæìÂÖ•‰∫ã‰ª∂Â§ÑÁêÜ
			onMajorInput(e) {
				this.searchText = e.detail.value;
				this.showMajorList = true;
				if (this.searchText.length >= 2) {
					this.searchMajors(this.searchText);
				}
			},
			
			selectMajor(major) {
				this.searchText = major;
				this.selectedMajor = major;
				this.showMajorList = false;
				// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
				uni.setStorageSync('step4_major', major);
			},
			selectMode(mode) {
				this.selectedMode = mode;
				// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
				uni.setStorageSync('step4_mode', mode);
			},
			prevPage() {
				uni.navigateBack();
			},
			nextPage() {
				if (!this.selectedMajor || !this.selectedMode) {
					uni.showToast({
						title: 'ËØ∑ÈÄâÊã©ÂÆåÊï¥‰ø°ÊÅØ',
						icon: 'none'
					});
					return;
				}
				uni.navigateTo({
					url: '/pages/selection/step5'
				});
			}
		}
	}
</script>

<style>
	.container {
		padding: 30rpx;
	}
	
	.progress-bar {
		text-align: center;
		margin-bottom: 40rpx;
	}
	
	.progress-text {
		font-size: 32rpx;
		color: #FF9853;
		background: #FFF;
		padding: 10rpx 30rpx;
		border-radius: 30rpx;
	}
	
	.question-container {
		margin-top: 40rpx;
	}
	
	.emoji {
		font-size: 48rpx;
		margin-bottom: 20rpx;
	}
	
	.question {
		font-size: 36rpx;
		font-weight: bold;
		color: #333;
		margin-bottom: 40rpx;
		display: block;
	}
	
	.second-question {
		margin-top: 60rpx;
	}
	
	.search-box {
		margin: 20rpx 0;
	}
	
	.search-input {
		width: 100%;
		height: 80rpx;
		background: #F5F5F5;
		border-radius: 40rpx;
		padding: 0 30rpx;
		font-size: 28rpx;
	}
	
	.major-list {
		max-height: 300rpx;
		background: #FFFFFF;
		border-radius: 20rpx;
		box-shadow: 0 2rpx 12rpx rgba(0, 0, 0, 0.1);
	}
	
	.major-item {
		padding: 20rpx 30rpx;
		font-size: 28rpx;
		border-bottom: 1rpx solid #EEEEEE;
	}
	
	.major-item:active {
		background: #F5F5F5;
	}
	
	.options-container {
		display: flex;
		flex-wrap: wrap;
		gap: 20rpx;
		margin-top: 30rpx;
		justify-content: center;
	}
	
	.option-item {
		width: 200rpx;
		height: 80rpx;
		line-height: 80rpx;
		text-align: center;
		background: #F5F5F5;
		border-radius: 40rpx;
		font-size: 28rpx;
		color: #333;
	}
	
	.active {
		background: #007AFF;
		color: #FFFFFF;
	}
	
	.button-group {
		position: fixed;
		bottom: 40rpx;
		left: 0;
		right: 0;
		padding: 0 30rpx;
		display: flex;
		justify-content: space-between;
	}
	
	.prev-btn, .next-btn {
		width: 45%;
		height: 90rpx;
		line-height: 90rpx;
		border-radius: 45rpx;
		font-size: 32rpx;
	}
	
	.prev-btn {
		background: #FFFFFF;
		color: #007AFF;
		border: 1rpx solid #007AFF;
	}
	
	.next-btn {
		background: #007AFF;
		color: #FFFFFF;
	}
</style> 